@import '../utilities/colors.scss';

.home
{
    // Was giving scrollbars at 100 and I cba.
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;

    &__text
    {
        font-size: 1.5rem;
        font-family: sans-serif;
        font-weight: bold;
        text-align: center;
        padding-bottom: 1rem;
        margin: 0;

        &:not(:first-of-type) 
        {
            padding-top: 1rem;
        }
    }

    &__container
    {
        width: 50%;
        min-width: 30rem;
        min-height: 70%;
        display: flex;
        flex-direction: column;
        background-color: $color_light_gray;
        box-shadow: 0.75rem 0.75rem 0.75rem 0.1rem $color_gray;
        border: 0.1rem solid $color_gray;
        border-radius: 1rem;
        padding: 1rem;

        @media (max-width: 550px) 
        {
            min-width: 20rem;
            width: 85%;
        }

        @media (max-width: 375px) 
        {
            min-width: 14rem;
        }
    }

    // Used for mobile layout.
    &__subcontainer
    {
        gap: 1rem;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;

        // Keeps 'Solution' text in place,
        // because '&:not(:first-of-type)'
        // is not affectting it anymore.
        padding-bottom: 1rem;

        @media (max-width: 550px) 
        {
            flex-direction: row;
        }
    }

    &__symbols-container
    {
        display: flex;
        flex-direction: column;
    }

    &__symbols
    {
        width: 100%;
        display: flex;
        justify-content: center;
        gap: 1rem;

        @media (max-width: 550px) 
        {
            flex-direction: column;
        }
    }

    &__result
    {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;

        & p
        {
            font-size: 1.5rem;
            font-family: sans-serif;
            text-align: center;
            padding: 0.25rem;
            margin: 0;

            @media (max-width: 375px) 
            {
                font-size: 1.25rem;
            }
        }
    }
}